import { css } from 'linaria';

export const uppercase = css`
  text-transform: uppercase;
`;

export const fluid = css`
  padding-right: 20px;
  padding-left: 20px;
  max-width: 1280px;

  @media (min-width: 768px) {
    padding-right: 50px;
    padding-left: 50px;
  }

  @media (min-width: 1280px) {
    padding-right: 100px;
    padding-left: 100px;
  }
`;
