import BoxesLoader from '@components/atoms/boxes-loader';
import { styled } from 'linaria/react';

const StyledLoaderContainer = styled.div`
  min-height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  position: fixed;
  top: 0;
  z-index: 100;
`;

const PageLoader = ({ children }) => {
  const [loaded, setLoaded] = React.useState(false);

  React.useEffect(() => {
    setTimeout(() => {
      setLoaded(true);
    }, 0);
  }, []);

  return children;
  
  return (
    <StyledLoaderContainer>
      <BoxesLoader />
    </StyledLoaderContainer>
  );
  // return (
  //   <Transition
  //     config={{ ...config.slow, duration: 500 }}
  //     items={loaded}
  //     from={{ opacity: 1 }}
  //     enter={{ opacity: 1 }}
  //     leave={{ opacity: 0 }}
  //   >
  //     {(show) =>
  //       show
  //         ? (props) => (
  //             <animated.div style={props} className='main-container'>
  //               {children}
  //             </animated.div>
  //           )
  //         : (props) => (
  //             <animated.div className='preloader-wrapper' style={props}>
  //               <BoxesLoader />
  //             </animated.div>
  //           )
  //     }
  //   </Transition>
  // );
};

export default PageLoader;
