import React from 'react';
import Typography from '@components/atoms/typography';
import Section from '@components/atoms/section';
import Container from '@components/atoms/container';
import { media } from '@styles/media';
import Button from '@components/atoms/button';
import { styled } from 'linaria/react';
import Image from '@components/atoms/image';
import Svg from '@components/atoms/svg';

const StyledFooter = styled.footer`
  padding: 40px 0;
  background-image: var(--base-gradient-color);
`;

const StyledFooterContainer = styled(Container)`
  .text1 {
    font-size: 14px;
    line-height: 140%;
  }

  .subtitle2 {
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    margin-bottom: 16px;
  }
`;

const StyledQuickLinksContainer = styled.div``;
const StyledQuickLinksList = styled.ul`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 16px 50px;
`;
const StyledQuickLinksListItem = styled.li``;

const QuickLinks = () => {
  return (
    <StyledQuickLinksContainer>
      <Typography as='h3' className='subtitle2'>
        Quick Links
      </Typography>
      <StyledQuickLinksList>
        <StyledQuickLinksListItem>
          <Typography as='a' className='text1'>
            World Link Coin ICO
          </Typography>
        </StyledQuickLinksListItem>
        <StyledQuickLinksListItem>
          <Typography as='a' className='text1'>
            Team
          </Typography>
        </StyledQuickLinksListItem>
        <StyledQuickLinksListItem>
          <Typography as='a' className='text1'>
            Whitepaper
          </Typography>
        </StyledQuickLinksListItem>
        <StyledQuickLinksListItem>
          <Typography as='a' className='text1'>
            How It Works
          </Typography>
        </StyledQuickLinksListItem>
        <StyledQuickLinksListItem>
          <Typography as='a' className='text1'>
            FAQ
          </Typography>
        </StyledQuickLinksListItem>
        <StyledQuickLinksListItem>
          <Typography as='a' className='text1'>
            Tokens
          </Typography>
        </StyledQuickLinksListItem>
        <StyledQuickLinksListItem>
          <Typography as='a' className='text1'>
            Contact
          </Typography>
        </StyledQuickLinksListItem>
        <StyledQuickLinksListItem>
          <Typography as='a' className='text1'>
            Road Map
          </Typography>
        </StyledQuickLinksListItem>
      </StyledQuickLinksList>
    </StyledQuickLinksContainer>
  );
};

const StyledSubscribeNewsLetterContainer = styled.div`
  margin-top: 30px;
  width: 100%;
`;

const InputText = styled.input``;

const SubcribeInputContainer = styled.div`
  button {
    text-transform: uppercase;
    margin-top: 16px;
  }
`;

const SubscribeNewsletter = () => {
  return (
    <StyledSubscribeNewsLetterContainer>
      <Typography as='h3' className='subtitle2' align='left'>
        Subscribe Our Newsletter
      </Typography>
      <SubcribeInputContainer>
        <InputText />
        <Button variant='primary'>SUBSCRIBE</Button>
      </SubcribeInputContainer>
    </StyledSubscribeNewsLetterContainer>
  );
};

const StyledSocialMediaContainer = styled.div`
  margin-top: 16px;
  width: 100%;
`;

const StyledSocialIcon = styled(Svg)`
  width: 42px;
  height: 42px;
  border: 1px solid #61bed9;
  border-radius: 60px;
`;

const StyledSocialList = styled.ul`
  display: flex;

  li + li {
    margin-left: 12px;
  }
`;

const SocialMedia = () => {
  return (
    <StyledSocialMediaContainer>
      <Typography as='h3' className='subtitle2' align='left'>
        Social Media
      </Typography>
      <StyledSocialList>
        <li>
          <StyledSocialIcon />
        </li>
        <li>
          <StyledSocialIcon />
        </li>
        <li>
          <StyledSocialIcon />
        </li>
      </StyledSocialList>
    </StyledSocialMediaContainer>
  );
};

const Footer = () => {
  return (
    <StyledFooter variant='sm' as='footer'>
      <StyledFooterContainer isFluid>
        <QuickLinks />
        <SubscribeNewsletter />
        <SocialMedia />
      </StyledFooterContainer>
    </StyledFooter>
  );
};

export default Footer;
