import React from 'react';

// import '../../styles/_main.scss';

import { styled } from 'linaria/react';

const StyledLayout = styled.div`
  :global() {
    :root {
      --primary-text-color: #fff;

      --primary-bg-color: #fff;
      --secondary-bg-color: linear-gradient(
        240.17deg,
        #2275cb 1.2%,
        #021a83 100%
      );

      --accent-color: #2275cb;
      --ligthen-accent-color: #61bed9;
      --darken-accent-color: #021b84;

      --primary-text-color: #fff;
      --secondary-text-color: #61bed9;

      --base-font-family: 'Poppins', sans-serif;
      --base-title-color: #021b84;
      --secondary-color: #61bed9;
      --base-text-color: #ffffff;
      --add-color-text: #61bed9;
      --base-gradient-color: linear-gradient(
        240.17deg,
        #2275cb 1.2%,
        #021a83 100%
      );
    }

    * {
      box-sizing: border-box;
      margin: 0;
      padding: 0;
      outline: none;

      &:before,
      &:after {
        box-sizing: border-box;
      }
    }

    html,
    body {
      margin: 0;
      height: 100%;
      width: 100%;
    }

    html {
      font-size: 16px;
      transition: linear all 0.1s;
      -webkit-font-smoothing: antialiased;
    }

    body {
      overflow-x: hidden;
    }

    ul {
      list-style-type: none;
    }

    a {
      text-decoration: none;
      color: inherit;
      cursor: pointer;
    }
  }

  @import url('https://fonts.googleapis.com/css?family=Poppins:400,700&display=swap');

  padding: 0;
  margin: 0;
  font-family: var(--base-font-family);
  width: 100vw;
  height: 100%;

  &.locked {
    position: relative;
    overflow: hidden;
    height: 100%;
  }

  &.locked .container {
    position: relative;
    overflow: hidden;
    height: 100%;
  }
`;

const StyledMainContainer = styled.div`
  display: grid;
  grid-template-rows: 60px 1fr auto;
  grid-template-areas:
    'header'
    'main'
    'footer';
  min-height: 100vh;

  & > header {
    grid-area: header;
    height: 60px;
  }

  & > main {
    grid-area: main;

    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100vw;
  }

  & > footer {
    /* height: 100% */
    grid-area: footer;
  }
`;

const LayoutTemplate = ({ header, footer, main, loader }) => {
  const content = (
    <StyledMainContainer>
      {header}
      {main}
      {footer}
    </StyledMainContainer>
  );

  return (
    <StyledLayout>
      {loader && loader.call ? loader(content) : content}
    </StyledLayout>
  );
};

export default LayoutTemplate;
