import React from 'react';
import { styled } from 'linaria/react';

const StyledSvgWrapper = styled.div`
  svg {
    width: 100%;
    height: 100%;
  }
`;

const Svg = ({ svg, children, ...props }) => {
  return <StyledSvgWrapper {...props}>{svg}</StyledSvgWrapper>;
};

export default Svg;
