import React from 'react';
import { styled } from 'linaria/react';
import { media } from '@styles/media';
import { cx } from 'linaria';

const StyledSection = styled.section`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  padding-top: 40px;
  padding-bottom: 40px;

  ${media.up('md')} {
    padding: 50px 0;
  }

  ${media.up('xlg')} {
    padding: 100px 0;
  }

  &.sm {
    ${media.up('xlg')} {
      padding: 50px 0;
    }
  }

  &.bg {
    padding: 50px 0;

    ${media.up('md')} {
      padding: 70px 0;
    }

    ${media.up('xlg')} {
      padding: 120px 0;
    }
  }
`;

const Section = ({ children, className, variant, ...props }) => {
  return (
    <StyledSection {...props} className={cx(variant && variant, className)}>
      {children}
    </StyledSection>
  );
};

Section.defaultProps = {
  variant: null,
};

export default Section;
