import React from 'react';

import { styled } from 'linaria/react';
import { media } from '@styles/media';

export const StyledTypography = styled.span`
  color: var(--primary-text-color);
  font-family: var(--base-font-family);

  width: 100%;

  &.title1,
  &.title2 {
    line-height: 120%;
    font-size: 1.5rem;
    font-weight: bold;
  }

  &.title1 {
    ${media.up('md')} {
      font-size: 2rem;
      line-height: 48px;
    }
  }

  &.title2 {
    ${media.up('md')} {
      font-size: 2.2rem;
      line-height: 60px;
    }
  }

  &.subtitle1,
  &.subtitle2 {
    line-height: 21px;
    color: var(--secondary-text-color);
    font-weight: normal;
  }

  &.subtitle1 {
    font-size: 0.9rem;

    ${media.up('md')} {
      font-size: 1rem;
    }
  }

  &.subtitle2 {
    font-size: 1rem;
  }

  &.text1,
  &.text2 {
    font-weight: normal;
    font-size: 0.9rem;
  }

  &.text1 {
    line-height: 24px;
    opacity: 0.6;

    ${media.up('md')} {
      font-size: 1rem;
    }
  }

  &.text2 {
    line-height: 180%;
    opacity: 0.8;
  }
`;

const Typography = ({ children, align, style, ...props }) => {
  const newProps = { ...props };
  const userStyle = { ...style, ...(align && { textAlign: align }) };

  if (Object.keys(userStyle > 0)) {
    newProps.style = userStyle;
  }

  return <StyledTypography {...newProps}>{children}</StyledTypography>;
};

Typography.defaultProps = {
  as: 'span',
};

export default Typography;
