import React from 'react';
import { cx } from 'linaria';
import { styled } from 'linaria/react';
import { fluid } from '@styles/helpers';

const StyledContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const Container = ({ children, isFluid, className, ...props }) => {
  return (
    <StyledContainer {...props} className={cx(className, isFluid && fluid)}>
      {children}
    </StyledContainer>
  );
};

Container.defaultProps = {
  isFluid: false,
};

export default Container;
