import React from 'react';
import Link from '@components/atoms/link';
import { styled } from 'linaria/react';
import Button from '@components/atoms/button';
import Image from '@components/atoms/image';
import Container from '@components/atoms/container';
import HamburgerMenu from '@components/molecules/hamburger-menu';
import { media } from '@styles/media';

const StyledLogoWrapper = styled.div``;
const StyledLogoImage = styled(Image)``;

const StyledNavContainer = styled.nav`
  margin-left: auto;
  margin-right: 75px;
  display: flex;
  position: relative;
  height: 100%;

  .nav-link {
    height: 100%;
    color: var(--base-text-color);
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    justify-content: center;
    opacity: 0.6;
    margin-right: 25px;
    transition: ease all 0.3s;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.02em;
    font-style: normal;
    position: relative;

    &:after {
      position: absolute;
      width: 100%;
      bottom: 0;
      content: '';
      border-bottom: solid 1px var(--add-color-text);
      transform: scaleX(0);
      transform-origin: 0 50%;
      transition: transform 250ms ease-in-out;
    }

    &:hover {
      color: var(--add-color-text);
      opacity: 1;

      &:after {
        transform: scaleX(1);
      }
    }

    &.active {
      color: var(--add-color-text);
      opacity: 1;
      border-bottom: 1px solid var(--add-color-text);
    }
  }
`;

const StyledNavList = styled.ul`
  display: flex;
  flex-direction: row;
`;

const StyledNavListItem = styled.li``;

const NavLink = ({ children, to }) => {
  return (
    <Link className='nav-link' activeClassName='active' to={to}>
      {children}
    </Link>
  );
};

const LoginButton = styled(Button)`
  margin-left: auto;
`;

const StyledContainer = styled(Container)`
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  margin: 0 auto;
  padding-right: 30px;

  .hamburger {
    margin-left: 30px;
    margin-top: 2px;
  }

  ${media.up('md')} {
    .hamburger {
      display: none;
    }
  }

  ${media.down('xlg')} {
    ${StyledNavContainer} {
      display: none;
    }
  }

  ${media.down('md')} {
    ${LoginButton} {
      display: none;
    }
  }
`;

const StyledHeader = styled.header`
  background: var(--base-title-color);
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.15);
  display: flex;
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  height: auto;
`;

const Header = () => {
  return (
    <StyledHeader>
      <StyledContainer isFluid>
        <StyledLogoWrapper>
          <Link to='/'>
            <StyledLogoImage alt=' ' className='logo' src='/images/logo.png' />
          </Link>
        </StyledLogoWrapper>
        <StyledNavContainer>
          <StyledNavList>
            <StyledNavListItem>
              <NavLink to='/'>Main</NavLink>
            </StyledNavListItem>
            <StyledNavListItem>
              <NavLink to='/investmen'>Investmen</NavLink>
            </StyledNavListItem>
            <StyledNavListItem>
              <NavLink to='/about'>About</NavLink>
            </StyledNavListItem>
            <StyledNavListItem>
              <NavLink to='/how'>How</NavLink>
            </StyledNavListItem>
            <StyledNavListItem>
              <NavLink to='/road-map'>Road Map</NavLink>
            </StyledNavListItem>
            <StyledNavListItem>
              <NavLink to='/token'>Token</NavLink>
            </StyledNavListItem>
            <StyledNavListItem>
              <NavLink to='/team'>Team</NavLink>
            </StyledNavListItem>
            <StyledNavListItem>
              <NavLink to='/faq'>FAQ</NavLink>
            </StyledNavListItem>
            <StyledNavListItem>
              <NavLink to='/contact'>Contact</NavLink>
            </StyledNavListItem>
          </StyledNavList>
        </StyledNavContainer>
        <LoginButton variant='primary'>Login</LoginButton>
        <HamburgerMenu />
      </StyledContainer>
    </StyledHeader>
  );
};

export default Header;
